import { GoogleGenAI, Type } from "@google/genai";
import { Listing } from "../types";

// Lazy Gemini client – opcional, só se houver API key configurada
const GEMINI_API_KEY =
  process.env.NEXT_PUBLIC_GEMINI_API_KEY || process.env.API_KEY;

let ai: GoogleGenAI | null = null;

const getClient = () => {
  if (!GEMINI_API_KEY) {
    // Sem chave, devolvemos null e os consumidores fazem fallback gracioso
    return null;
  }
  if (!ai) {
    ai = new GoogleGenAI({ apiKey: GEMINI_API_KEY });
  }
  return ai;
};

// 1. Smart Search: Convert natural language to structured filters
export const parseSearchQuery = async (query: string) => {
  const client = getClient();
  if (!client) {
    console.warn(
      "[Gemini] API key não configurada – a pesquisa inteligente será ignorada."
    );
    return null;
  }

  try {
    const response = await client.models.generateContent({
      model: "gemini-2.5-flash",
      contents: `Extract search filters from this user query: "${query}".
      Return JSON with keys: location (string), minPrice (number), maxPrice (number), category (string matching one of: amazing-pools, beachfront, cabins, omg, camping, design, tiny-homes).`,
      config: {
        responseMimeType: "application/json",
        responseSchema: {
          type: Type.OBJECT,
          properties: {
            location: { type: Type.STRING },
            minPrice: { type: Type.NUMBER },
            maxPrice: { type: Type.NUMBER },
            category: { type: Type.STRING },
          }
        }
      }
    });
    
    if (response.text) {
      return JSON.parse(response.text);
    }
    return null;
  } catch (error) {
    console.error("Gemini Search Error:", error);
    return null;
  }
};

// 2. Maps Grounding: Ask questions about the listing's surroundings
export const askAboutListingLocation = async (
  question: string, 
  listing: Listing
) => {
  const client = getClient();
  if (!client) {
    console.warn(
      "[Gemini] API key não configurada – fallback de resposta simples."
    );
    return {
      text:
        "Informações detalhadas sobre a vizinhança não estão disponíveis no momento. Verifique o mapa e a descrição do anúncio para mais contexto.",
      mapLinks: [],
    };
  }

  try {
    const response = await client.models.generateContent({
      model: "gemini-2.5-flash",
      contents: `User is asking about a listing located at ${listing.location} (Lat: ${listing.lat}, Lng: ${listing.lng}). 
      Question: "${question}".
      Provide a helpful, concise answer focusing on nearby places using Google Maps data. 
      If you find specific places, mention their names and rating if available.`,
      config: {
        tools: [{ googleMaps: {} }],
        toolConfig: {
          retrievalConfig: {
            latLng: {
              latitude: listing.lat,
              longitude: listing.lng
            }
          }
        }
      }
    });

    const text = response.text;
    const groundingChunks = response.candidates?.[0]?.groundingMetadata?.groundingChunks;
    
    // Extract map links if available
    const mapLinks: { title: string; uri: string }[] = [];
    if (groundingChunks) {
      groundingChunks.forEach((chunk: any) => {
        if (chunk.web?.uri && chunk.web?.title) {
           mapLinks.push({ title: chunk.web.title, uri: chunk.web.uri });
        }
        if (chunk.maps?.uri && chunk.maps?.title) { // Although strictly speaking we look for web uri often in maps grounding response too
             mapLinks.push({ title: chunk.maps.title, uri: chunk.maps.uri });
        }
      });
    }

    return { text, mapLinks };

  } catch (error) {
    console.error("Gemini Maps Error:", error);
    return { text: "Sorry, I couldn't access map data right now.", mapLinks: [] };
  }
};
