import React, { useState } from 'react';
import { Search, Globe, Menu, UserCircle, Sparkles, X } from 'lucide-react';
import { parseSearchQuery } from '../services/geminiService';
import { SearchFilters } from '../types';

interface NavbarProps {
  onSearch: (filters: SearchFilters) => void;
  isScrolled: boolean;
  onSearchStart: () => void;
  onSearchEnd: () => void;
}

const Navbar: React.FC<NavbarProps> = ({ onSearch, isScrolled, onSearchStart, onSearchEnd }) => {
  const [searchOpen, setSearchOpen] = useState(false);
  const [prompt, setPrompt] = useState('');
  const [loading, setLoading] = useState(false);

  const handleSmartSearch = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!prompt.trim()) return;

    setLoading(true);
    onSearchStart();

    try {
      const filters = await parseSearchQuery(prompt);
      
      if (filters) {
        onSearch(filters);
        setSearchOpen(false);
        setPrompt('');
      }
    } catch (error) {
      console.error("Search failed", error);
    } finally {
      setLoading(false);
      onSearchEnd();
    }
  };

  return (
    <>
      <nav className={`fixed top-0 w-full z-40 transition-all duration-300 bg-white ${isScrolled ? 'shadow-sm py-4' : 'py-5'}`}>
        <div className="max-w-[2520px] mx-auto xl:px-20 md:px-10 sm:px-2 px-4">
          <div className="flex flex-row items-center justify-between gap-3 md:gap-0">
            {/* Logo */}
            <div className="cursor-pointer" onClick={() => window.location.reload()}>
              <div className="flex items-center gap-1 text-[#FF385C]">
                 <svg viewBox="0 0 32 32" xmlns="http://www.w3.org/2000/svg" aria-hidden="true" role="presentation" focusable="false" style={{display: 'block', height: '32px', width: '32px', fill: 'currentcolor'}}><path d="M16 1c2.008 0 3.463.963 4.751 3.269l.533 1.025c1.954 3.83 6.114 12.54 7.1 14.836l.145.353c.667 1.591.91 3.162.723 4.691-.237 1.944-1.295 3.644-2.98 4.79-1.488 1.011-3.268 1.536-5.172 1.536-2.848 0-5.44-1.178-7.1-3.218-1.66 2.04-4.252 3.218-7.1 3.218-1.904 0-3.684-.525-5.172-1.536C.167 28.847-.89 27.147-1.128 25.203c-.187-1.529.056-3.1.723-4.691l.145-.353c.986-2.296 5.146-11.006 7.1-14.836l.533-1.025C8.537 1.963 9.992 1 16 1zm0 2c-1.239 0-2.053.539-2.987 2.211-.07.124-3.606 6.99-6.953 13.902l-.147.31c-.53 1.272-.716 2.502-.572 3.684.15 1.228.835 2.296 1.928 3.038.971.66 2.115 1.006 3.33 1.006 2.094 0 4.026-.951 5.4-2.658l.412-.533.412.533c1.374 1.707 3.306 2.658 5.4 2.658 1.215 0 2.36-.346 3.33-1.006 1.093-.742 1.778-1.81 1.928-3.038.144-1.182-.042-2.412-.572-3.684l-.147-.31c-3.347-6.912-6.883-13.778-6.953-13.902C18.053 3.539 17.24 3 16 3zm0 20c-1.104 0-2 .896-2 2s.896 2 2 2 2-.896 2-2-.896-2-2-2z"></path></svg>
                 <span className="font-bold text-xl hidden lg:block text-[#FF385C]">airbnb</span>
              </div>
            </div>

            {/* Search Bar - Toggle Button */}
            <div 
              onClick={() => setSearchOpen(true)}
              className="border-[1px] w-full md:w-auto py-2 rounded-full shadow-sm hover:shadow-md transition cursor-pointer"
            >
              <div className="flex flex-row items-center justify-between">
                <div className="text-sm font-semibold px-6">Anywhere</div>
                <div className="hidden sm:block text-sm font-semibold px-6 border-x-[1px] flex-1 text-center">Any week</div>
                <div className="text-sm pl-6 pr-2 text-gray-600 flex flex-row items-center gap-3">
                  <div className="hidden sm:block">Add guests</div>
                  <div className="p-2 bg-[#FF385C] rounded-full text-white">
                    <Search size={18} />
                  </div>
                </div>
              </div>
            </div>

            {/* User Menu */}
            <div className="flex flex-row items-center gap-4 font-semibold text-gray-500">
               <div className="text-sm cursor-pointer py-3 px-4 rounded-full hover:bg-gray-100 transition hidden md:block">
                 Airbnb your home
               </div>
               <div className="cursor-pointer py-3 px-3 rounded-full hover:bg-gray-100 transition hidden md:block">
                 <Globe size={18} />
               </div>
               <div className="flex flex-row items-center gap-3 border-[1px] border-neutral-200 p-4 md:py-1 md:px-2 rounded-full hover:shadow-md transition cursor-pointer">
                 <Menu />
                 <div className="hidden md:block">
                   <UserCircle size={30} className="text-gray-500" />
                 </div>
               </div>
            </div>
          </div>
        </div>
      </nav>

      {/* Smart Search Overlay */}
      {searchOpen && (
        <div className="fixed inset-0 z-50 bg-black/50 flex items-start justify-center pt-32 animate-in fade-in duration-200" onClick={(e) => {
          if (e.target === e.currentTarget) setSearchOpen(false);
        }}>
          <div className="bg-white rounded-2xl w-full max-w-2xl p-6 shadow-2xl mx-4 relative overflow-hidden">
             <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-pink-500 via-red-500 to-yellow-500"></div>
             
             <button onClick={() => setSearchOpen(false)} className="absolute top-4 right-4 p-2 hover:bg-gray-100 rounded-full">
               <X size={20} />
             </button>

             <h2 className="text-2xl font-bold mb-2 flex items-center gap-2">
               <Sparkles className="text-yellow-500 fill-yellow-500" />
               Where to next?
             </h2>
             <p className="text-gray-500 mb-6">Describe your dream trip and let AI handle the filters.</p>
             
             <form onSubmit={handleSmartSearch} className="relative">
               <textarea 
                 value={prompt}
                 onChange={(e) => setPrompt(e.target.value)}
                 placeholder="e.g., I want a beachfront villa in Bali for under $400/night with a pool..."
                 className="w-full p-4 pr-14 bg-gray-50 rounded-xl border border-gray-200 focus:outline-none focus:ring-2 focus:ring-black resize-none h-32 text-lg"
                 autoFocus
               />
               <button 
                 type="submit" 
                 disabled={loading || !prompt.trim()}
                 className={`absolute bottom-4 right-4 p-3 rounded-full text-white transition-all duration-300 ${loading ? 'bg-gray-300' : 'bg-[#FF385C] hover:scale-105'}`}
               >
                 {loading ? (
                   <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                 ) : (
                   <Search size={20} />
                 )}
               </button>
             </form>

             <div className="mt-6 flex gap-2 flex-wrap">
               <span className="text-xs font-bold text-gray-400 uppercase tracking-wider">Try asking:</span>
               <button onClick={() => setPrompt("Cozy cabin in the woods for a weekend getaway")} className="text-sm bg-gray-100 hover:bg-gray-200 px-3 py-1 rounded-full text-gray-700 transition">
                 "Cozy cabin in the woods"
               </button>
               <button onClick={() => setPrompt("Luxury apartment in New York near Central Park")} className="text-sm bg-gray-100 hover:bg-gray-200 px-3 py-1 rounded-full text-gray-700 transition">
                 "Luxury apartment in NY"
               </button>
             </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Navbar;