import React, { useState, useEffect } from 'react';
import Navbar from './components/Navbar';
import CategoryBar from './components/CategoryBar';
import ListingCard from './components/ListingCard';
import ListingModal from './components/ListingModal';
import FilterModal from './components/FilterModal';
import Footer from './components/Footer';
import { LISTINGS } from './constants';
import { CategoryId, SearchFilters, Listing } from './types';
import { Map } from 'lucide-react';

const App: React.FC = () => {
  const [selectedCategory, setSelectedCategory] = useState<CategoryId>('amazing-pools');
  const [filteredListings, setFilteredListings] = useState<Listing[]>(LISTINGS);
  const [selectedListing, setSelectedListing] = useState<Listing | null>(null);
  const [isScrolled, setIsScrolled] = useState(false);
  const [wishlist, setWishlist] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  
  // Search & Filter State
  const [filters, setFilters] = useState<SearchFilters>({});
  const [isFilterModalOpen, setIsFilterModalOpen] = useState(false);

  // Scroll handler for navbar styling
  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 10);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Load wishlist from localStorage
  useEffect(() => {
    const savedWishlist = localStorage.getItem('wishlist');
    if (savedWishlist) {
      try {
        setWishlist(JSON.parse(savedWishlist));
      } catch (error) {
        console.error('Failed to parse wishlist from localStorage', error);
      }
    }
  }, []);

  const toggleWishlist = (e: React.MouseEvent, id: string) => {
    e.stopPropagation();
    setWishlist(prev => {
      const newWishlist = prev.includes(id) 
        ? prev.filter(item => item !== id) 
        : [...prev, id];
      localStorage.setItem('wishlist', JSON.stringify(newWishlist));
      return newWishlist;
    });
  };

  // Main Filter Logic
  useEffect(() => {
    let result = LISTINGS;

    // 1. Filter by Category
    if (selectedCategory !== 'trending') {
       result = result.filter(l => l.category === selectedCategory);
    }

    // 2. Filter by Search Criteria (AI or Manual)
    if (filters.category) {
       // Best effort text category matching if AI returns it
       const catMatch = filters.category.toLowerCase().replace(' ', '-');
       // If AI suggests a different category than selected, we prioritize AI filter if user searched
       result = result.filter(l => l.category.includes(catMatch));
    }

    if (filters.location) {
      result = result.filter(l => l.location.toLowerCase().includes(filters.location!.toLowerCase()));
    }

    if (filters.minPrice !== undefined) {
      result = result.filter(l => l.price >= filters.minPrice!);
    }
    
    if (filters.maxPrice !== undefined) {
      result = result.filter(l => l.price <= filters.maxPrice!);
    }

    setFilteredListings(result);
  }, [selectedCategory, filters]);

  const handleSmartSearch = (newFilters: SearchFilters) => {
    // When smart search happens, we update filters
    setFilters(newFilters);
  };

  const handleManualFilterApply = (manualFilters: SearchFilters) => {
    // Merge manual filters with existing filters (e.g. location from search)
    setFilters(prev => ({
      ...prev,
      minPrice: manualFilters.minPrice,
      maxPrice: manualFilters.maxPrice
    }));
  };

  return (
    <div className="min-h-screen flex flex-col text-neutral-800 bg-white">
      <Navbar 
        onSearch={handleSmartSearch} 
        isScrolled={isScrolled} 
        onSearchStart={() => setIsLoading(true)}
        onSearchEnd={() => setIsLoading(false)}
      />
      
      <main className="flex-1 pb-20 relative">
        <CategoryBar 
          selected={selectedCategory} 
          onSelect={(id) => {
            setSelectedCategory(id);
          }}
          onFilterClick={() => setIsFilterModalOpen(true)}
        />

        <div className="max-w-[2520px] mx-auto xl:px-20 md:px-10 sm:px-2 px-4 mt-4">
          {isLoading ? (
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 2xl:grid-cols-6 gap-8">
              {[...Array(12)].map((_, i) => (
                <div key={i} className="col-span-1 flex flex-col gap-2 animate-pulse">
                   <div className="aspect-square w-full bg-gray-200 rounded-xl relative overflow-hidden"></div>
                   <div className="flex justify-between mt-2">
                      <div className="h-4 bg-gray-200 rounded w-2/3"></div>
                      <div className="h-4 bg-gray-200 rounded w-8"></div>
                   </div>
                   <div className="h-4 bg-gray-200 rounded w-1/2"></div>
                   <div className="h-4 bg-gray-200 rounded w-1/3 mt-1"></div>
                </div>
              ))}
            </div>
          ) : filteredListings.length === 0 ? (
            <div className="min-h-[40vh] flex flex-col items-center justify-center text-center">
              <div className="text-2xl font-bold mb-2">No matches found</div>
              <p className="text-gray-500 max-w-md">Try adjusting your filters or search query.</p>
              <button 
                onClick={() => { 
                  setSelectedCategory('trending'); 
                  setFilters({});
                }}
                className="mt-6 px-6 py-3 bg-black text-white rounded-lg hover:bg-gray-800 transition"
              >
                Clear all filters
              </button>
            </div>
          ) : (
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 2xl:grid-cols-6 gap-8">
              {filteredListings.map((listing) => (
                <ListingCard 
                  key={listing.id} 
                  data={listing} 
                  isWishlisted={wishlist.includes(listing.id)}
                  onToggleWishlist={(e) => toggleWishlist(e, listing.id)}
                  onClick={() => setSelectedListing(listing)} 
                />
              ))}
            </div>
          )}
        </div>
        
        {/* Floating Map Button (Visual Only) */}
        {!isLoading && filteredListings.length > 0 && (
          <div className="fixed bottom-10 left-1/2 transform -translate-x-1/2 z-30">
            <button className="bg-neutral-900 hover:scale-105 hover:bg-black text-white px-5 py-3.5 rounded-full font-semibold shadow-xl flex items-center gap-2 transition duration-300 text-sm">
              Show map <Map size={18} />
            </button>
          </div>
        )}
      </main>

      <Footer />

      {/* Listing Detail Modal */}
      {selectedListing && (
        <ListingModal 
          listing={selectedListing} 
          onClose={() => setSelectedListing(null)} 
        />
      )}

      {/* Filter Modal */}
      <FilterModal 
        isOpen={isFilterModalOpen}
        onClose={() => setIsFilterModalOpen(false)}
        currentFilters={filters}
        onApply={handleManualFilterApply}
      />
    </div>
  );
};

export default App;