import React, { useState, useEffect } from 'react';
import { X } from 'lucide-react';
import { SearchFilters } from '../types';

interface FilterModalProps {
  isOpen: boolean;
  onClose: () => void;
  currentFilters: SearchFilters;
  onApply: (filters: SearchFilters) => void;
}

const FilterModal: React.FC<FilterModalProps> = ({ isOpen, onClose, currentFilters, onApply }) => {
  const [minPrice, setMinPrice] = useState<string>(currentFilters.minPrice?.toString() || '');
  const [maxPrice, setMaxPrice] = useState<string>(currentFilters.maxPrice?.toString() || '');

  useEffect(() => {
    if (isOpen) {
      setMinPrice(currentFilters.minPrice?.toString() || '');
      setMaxPrice(currentFilters.maxPrice?.toString() || '');
    }
  }, [isOpen, currentFilters]);

  if (!isOpen) return null;

  const handleApply = () => {
    onApply({
      ...currentFilters,
      minPrice: minPrice ? Number(minPrice) : undefined,
      maxPrice: maxPrice ? Number(maxPrice) : undefined,
    });
    onClose();
  };

  const handleClear = () => {
    setMinPrice('');
    setMaxPrice('');
    onApply({
      ...currentFilters,
      minPrice: undefined,
      maxPrice: undefined,
    });
    onClose();
  };

  return (
    <div className="fixed inset-0 z-50 bg-black/50 flex items-center justify-center p-4 animate-in fade-in duration-200">
      <div className="bg-white w-full max-w-lg rounded-2xl shadow-xl overflow-hidden flex flex-col max-h-[90vh]">
        {/* Header */}
        <div className="flex items-center justify-between p-4 border-b">
          <button onClick={onClose} className="p-2 hover:bg-gray-100 rounded-full transition">
            <X size={16} />
          </button>
          <div className="font-bold text-lg">Filters</div>
          <div className="w-8"></div> {/* Spacer for centering */}
        </div>

        {/* Content */}
        <div className="p-6 overflow-y-auto">
          <section>
            <h3 className="text-xl font-semibold mb-4">Price range</h3>
            <p className="text-gray-500 text-sm mb-6">Nightly prices before fees and taxes</p>
            
            <div className="flex items-center gap-4">
              <div className="border rounded-xl px-3 py-2 flex-1 focus-within:ring-2 focus-within:ring-black">
                <label className="text-xs text-gray-500 block">Minimum</label>
                <div className="flex items-center">
                  <span className="text-sm mr-1">$</span>
                  <input
                    type="number"
                    value={minPrice}
                    onChange={(e) => setMinPrice(e.target.value)}
                    className="w-full outline-none text-sm"
                    placeholder="50"
                  />
                </div>
              </div>
              <div className="text-gray-400">-</div>
              <div className="border rounded-xl px-3 py-2 flex-1 focus-within:ring-2 focus-within:ring-black">
                <label className="text-xs text-gray-500 block">Maximum</label>
                <div className="flex items-center">
                  <span className="text-sm mr-1">$</span>
                  <input
                    type="number"
                    value={maxPrice}
                    onChange={(e) => setMaxPrice(e.target.value)}
                    className="w-full outline-none text-sm"
                    placeholder="1000+"
                  />
                </div>
              </div>
            </div>
          </section>
        </div>

        {/* Footer */}
        <div className="p-4 border-t flex justify-between items-center">
          <button 
            onClick={handleClear}
            className="text-sm font-semibold underline hover:text-gray-600 px-4 py-2"
          >
            Clear all
          </button>
          <button 
            onClick={handleApply}
            className="bg-black text-white px-8 py-3 rounded-lg font-semibold hover:bg-gray-800 transition"
          >
            Show places
          </button>
        </div>
      </div>
    </div>
  );
};

export default FilterModal;